import React from 'react';
import { OverlayTrigger, Tooltip, } from 'react-bootstrap'
import swal from "sweetalert";
import axios from "axios";

class Favorite extends React.Component {
  constructor() {
    super()
    this.state = {
        favorite: []
    }
}

componentDidMount(){
  this.setState({
    favorite: this.props.favorite
  })
}

  componentDidUpdate(prevProps){
    if(this.props.favorite !== prevProps.favorite){
      this.setState({
        favorite: this.props.favorite
      })
    }
  }

  renderTooltip = (props) => {

    return (
      <Tooltip id="button-tooltip" {...props}>
          {this.state.favorite === 1 &&
         <span>Remove from favorites</span>
          }
          {this.state.favorite ===  0  &&
         <span>Add to favorites</span>
          }
      </Tooltip>
    );
  }

  submitForm = (values, history) => {
    console.log(this.state.favorite)
    if(this.state.favorite === 0) {
      values = this.props
      axios
        .put(`${process.env.REACT_APP_URL}/api/favorite/add`, values)
        .then(res => {
          if(res.data.result === "success") {
            console.log(`Watch Later Added`)
            this.setState({
              favorite: 1})
            } 

            else if (res.data.result === "error") {
console.log('Watch Later Failed')  
          }  
           })
        .catch(error => {
          console.log(error);
        });
    } else {
      console.log(this.props)
      let config = {
        headers: {
          'Content-Type': 'application/json',
          'Username': `${this.props.user}`,
          'TmbId': `${this.props.movie.tmdbId}`
        }
      }
      axios
        .delete(`${process.env.REACT_APP_URL}/api/favorite/delete`, config)
        .then(res => {
          console.log(res.data)
          if(res.data.result === "success") {
            console.log("Watch Later Deleted")
            this.setState({
              favorite: 0
            })
        }}) 
          }
        
    };


  render() 
  {
    return (
   
        <OverlayTrigger
        placement="bottom"
        delay={{ show: 50, hide: 200 }}
        overlay={this.renderTooltip}
      >
        <a onClick={this.submitForm}> 
        {this.state.favorite === 0 &&

<svg width="20px" height="20px" viewBox="0 0 16 16" class="bi bi-heart" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
  <path fill-rule="evenodd" d="M8 2.748l-.717-.737C5.6.281 2.514.878 1.4 3.053c-.523 1.023-.641 2.5.314 4.385.92 1.815 2.834 3.989 6.286 6.357 3.452-2.368 5.365-4.542 6.286-6.357.955-1.886.838-3.362.314-4.385C13.486.878 10.4.28 8.717 2.01L8 2.748zM8 15C-7.333 4.868 3.279-3.04 7.824 1.143c.06.055.119.112.176.171a3.12 3.12 0 0 1 .176-.17C12.72-3.042 23.333 4.867 8 15z"/>
</svg>
  }
        {this.state.favorite === 1 &&
          <svg width="20px" height="20px" viewBox="0 0 16 16" class="bi bi-heart-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
  <path fill-rule="evenodd" d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z"/>
</svg>
  }
        </a>


      </OverlayTrigger>
    );
  }
}


export default Favorite;
 