import React from 'react';
import { OverlayTrigger, Tooltip, } from 'react-bootstrap'
import axios from "axios";

class watchLater extends React.Component {
  constructor() {
    super()
    this.state = {
        watchlater: []
    }
}

componentDidUpdate(prevProps){
  if(this.props.watchlater !== prevProps.watchlater){
    this.setState({
      watchlater: this.props.watchlater
    })
  }
}

  componentDidMount(){
    this.setState({
      watchlater: this.props.watchlater
    })
  }

  renderTooltip = (props) => {

    return (
      <Tooltip id="button-tooltip" {...props}>
          {this.state.watchlater === 1 &&
         <span>Remove from watch later list</span>
          }
          {this.state.watchlater ===  0  &&
         <span>Add to watch later list</span>
          }
      </Tooltip>
    );
  }

  submitForm = (values, history) => {
    console.log(this.state.watchlater)
    if(this.state.watchlater === 0) {
      values = this.props
      axios
        .put(`${process.env.REACT_APP_URL}/api/watchlater/add`, values)
        .then(res => {
          if(res.data.result === "success") {
            console.log(`Watch Later Added`)
            this.setState({
              watchlater: 1})
            } 

            else if (res.data.result === "error") {
console.log('Watch Later Failed')  
          }  
           })
        .catch(error => {
          console.log(error);
        });
    } else {
      console.log(this.props)
      let config = {
        headers: {
          'Content-Type': 'application/json',
          'Username': `${this.props.user}`,
          'TmbId': `${this.props.movie.tmdbId}`
        }
      }
      axios
        .delete(`${process.env.REACT_APP_URL}/api/watchlater/delete`, config)
        .then(res => {
          console.log(res.data)
          if(res.data.result === "success") {
            console.log("Watch Later Deleted")
            this.setState({
              watchlater: 0
            })
        }}) 
          }
        
    };


  render() 
  {
    return (
        <OverlayTrigger
        placement="bottom"
        delay={{ show: 50, hide: 200 }}
        overlay={this.renderTooltip}
      >
        <a onClick={this.submitForm}> 
        {this.state.watchlater === 0 &&

        <svg width="20px" height="20px" viewBox="0 0 16 16" class="bi bi-stopwatch" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
        <path fill-rule="evenodd" d="M6 .5a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 0 1H9v1.07A7.001 7.001 0 0 1 8 16 7 7 0 0 1 7 2.07V1h-.5A.5.5 0 0 1 6 .5zM8 3a6 6 0 1 0 .001 12A6 6 0 0 0 8 3zm0 2.1a.5.5 0 0 1 .5.5V9a.5.5 0 0 1-.5.5H4.5a.5.5 0 0 1 0-1h3V5.6a.5.5 0 0 1 .5-.5z"/>
        </svg> 
  }
        {this.state.watchlater === 1 &&
          <svg width="20px" height="20px" viewBox="0 0 16 16" class="bi bi-stopwatch-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
  <path fill-rule="evenodd" d="M6.5 0a.5.5 0 0 0 0 1H7v1.07A7.001 7.001 0 0 0 8 16 7 7 0 0 0 9 2.07V1h.5a.5.5 0 0 0 0-1h-3zm2 5.6a.5.5 0 1 0-1 0v2.9h-3a.5.5 0 0 0 0 1H8a.5.5 0 0 0 .5-.5V5.6z"/>
</svg>
  }
        </a>


      </OverlayTrigger>
    );
  }
}


export default watchLater;
 