import React, { Component } from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import swal from "sweetalert";
import Cookies from "js-cookie";
import "./login.css";
import { Col, Row, Container } from "react-bootstrap";
import { withRouter, Link } from "react-router-dom";

const LoginSchema = Yup.object().shape({
  username: Yup.string()
    .min(2, "username is Too Short!")
    .max(50, "username is Too Long!")
    .required("Username is required."),
  password: Yup.string().required("Password is required."),
});

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      alert: null,
    };
  }

  componentDidMount() {
    if (localStorage.getItem("TOKEN_KEY") != null) {
      return this.props.history.push("/dashboard");
    }
  }

  submitForm = async (values, history) => {
    values.username = values.username.toLowerCase();
    axios
      .post(`${process.env.REACT_APP_URL}/login`, values, values)
      .then((res) => {
        if (res.data.result === "success") {
          Cookies.set(
            "name",
            values.username[0].toUpperCase() + values.username.slice(1),
            { expires: 3 }
          );
          Cookies.set("TOKEN_KEY", res.data.token, { expires: 3 });
          localStorage.setItem("user", res.data.username);
          this.props.history.push("/dashboard");
        } else if (res.data.result === "error") {
          swal("Error!", res.data.message, "error");
        }
      })
      .catch((error) => {
        console.log(error);
        return swal("Error!", error.message, "error");
      });
  };
  showForm = ({
    values,
    errors,
    touched,
    handleChange,
    handleSubmit,
    setFieldValue,
    isSubmitting,
  }) => {
    return (
      <Container fluid>
        <Row className="outside">
          <Col className="loginBox" xs={10} md={6} lg={3} xl={2}>
            <Row>
              <Col>
                <h1>
                  <b>Last</b>Play<span style={{ fontSize: "14px" }}>.tv</span>
                </h1>
              </Col>
            </Row>
            <Row>
              <img
                src={process.env.PUBLIC_URL + "/logo100.png"}
                alt="logo"
                style={{ margin: "0 auto", marginBottom: "5%" }}
              />
            </Row>

            <Row>
              <form
                onSubmit={handleSubmit}
                style={{ marginBottom: "5%", margin: "0 auto" }}
              >
                <div className="registerLogin">
                  <input
                    type="text"
                    name="username"
                    onChange={handleChange}
                    value={values.username}
                    placeholder="Username"
                    className={
                      errors.username && touched.username
                        ? "form-control is-invalid"
                        : "form-control"
                    }
                  />
                  <hr style={{ margin: "0" }} />
                </div>
                <div className="registerLogin input-group mb-3 has-feedback">
                  <input
                    type="password"
                    name="password"
                    onChange={handleChange}
                    value={values.password}
                    placeholder="Password"
                    className={
                      errors.password && touched.password
                        ? "form-control is-invalid"
                        : "form-control"
                    }
                  />
                </div>

                <Col style={{ textAlign: "center" }}>
                  <button
                    type="submit"
                    disabled={isSubmitting}
                    className="btn btn-primary btn-block"
                    style={{ marginTop: "45px" }}
                  >
                    Login
                  </button>
                  <Link to="/password/forgot">
                    <span style={{ fontSize: "12px" }}>Forgot password?</span>
                  </Link>
                </Col>
                <div style={{ textAlign: "center" }}>
                  {errors.password && touched.password ? (
                    <small id="passwordHelp" class="text-danger">
                      <span>{errors.password}</span> <br />
                    </small>
                  ) : null}
                  {errors.username && touched.username ? (
                    <small id="passwordHelp" class="text-danger">
                      <span>{errors.username}</span>
                    </small>
                  ) : null}
                </div>
              </form>
            </Row>
          </Col>
        </Row>
      </Container>
    );
  };

  render() {
    return (
      <div>
        <Formik
          initialValues={{
            username: "",
            password: "",
          }}
          onSubmit={(values, { setSubmitting }) => {
            this.submitForm(values, this.props.history);
            setSubmitting(false);
          }}
          validationSchema={LoginSchema}
        >
          {(props) => this.showForm(props)}
        </Formik>
      </div>
    );
  }
}

export default withRouter(Login);
