import React, { Component } from "react";
import swal from "sweetalert";
import { withRouter, Link } from "react-router-dom";
import { Nav, Navbar, NavDropdown, ListGroup } from "react-bootstrap";
import Autocomplete from "react-autocomplete";
import Cookies from "js-cookie";
import "./header.css";
import {
  MdLocalMovies,
  MdLiveTv,
  MdHome,
  MdShare,
  MdLiveHelp,
} from "react-icons/md";

let menuStyle = {
  borderRadius: "",
  boxShadow: "0 2px 12px rgba(0, 0, 0, 0.1)",
  background: "rgba(255, 255, 255, 0.9)",
  padding: "0 0",
  fontSize: "90%",
  position: "fixed",
  overflow: "auto",
  paddingBottom: "-5px",
  maxHeight: "50%", // TODO: don't cheat, let it flow to the bottom
};

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: "",
      fetched: false,
      shows: [],
      movies: [],
      names: "",
    };
  }

  getFilteredItems = (props) => {
    const items = Autocomplete.prototype.getFilteredItems.call(this, props);
    return items.slice(0, props.limitItemsLength);
  };
  Logout = (props) => {
    swal("Logout?", {
      buttons: {
        nope: {
          text: "Yes",
          value: "yes",
        },
        sure: {
          text: "Not yet",
          value: "no",
        },
      },
    }).then((value) => {
      switch (value) {
        case "yes":
          localStorage.removeItem("user");
          Cookies.remove("TOKEN_KEY");
          this.props.history.push("/login");
          break;
        case "no":
          break;
        default:
      }
    });
  };

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.match.url !== this.props.match.url) {
      window.location.reload(false);
    }
  }

  componentDidMount() {
    const username = localStorage.getItem("user");
    this.setState({ name: username });
  }

  Changing = (e) => {
    this.setState({ value: e.target.value });
    console.log(e.target.value);
    if (e.target.value.length === 1 && this.state.shows.length === 0) {
      const user = Cookies.get("name");
      this.setState({ name: user });
      let urls = [
        fetch(
          `https://lp-server1.lastplay.tv/app/api/series?apikey=${process.env.REACT_APP_SONARR}`
        ),
        fetch(
          `https://lp-server1.lastplay.tv/mov/api/movie?apikey=${process.env.REACT_APP_RADARR}`
        ),
      ];
      Promise.all(urls, user)
        .then(([res1, res2]) => {
          return Promise.all([res1.json(), res2.json()]);
        })
        .then(([res1, res2]) => {
          this.setState({ fetched: true });
          res1.map((show) => {
            show["division"] = "tv";
            return this.state.shows.push(show);
          });
          res2.map((movie) => {
            movie["division"] = "movie";

            return this.state.shows.push(movie);
          });
        });
    }
  };

  render() {
    return (
      <div>
        <Navbar fixed="top" bg="dark" variant="dark" expand="lg">
          <Navbar.Brand>
            <img
              src={process.env.PUBLIC_URL + "/logo100.png"}
              className="headerimg"
              alt="logo"
              href="/dashboard"
            />
            <div className="site">
              <b>Last</b>Play
            </div>
            <div className="newSearch">
              <ListGroup variant="flush">
                <Autocomplete
                  items={this.state.shows}
                  shouldItemRender={(item, value) =>
                    item.title.toLowerCase().indexOf(value.toLowerCase()) >
                      -1 && value.slice(1, 10)
                  }
                  getItemValue={(item) => item.title}
                  renderItem={(item, items, highlighted) => (
                    <ListGroup.Item className="searchbar">
                      {item.division === "tv" && (
                        <Link
                          key={item.id}
                          className="stretched-link dropdowns"
                          to={`/tv/${item.id}`}
                        >
                          {" "}
                          <img
                            src={`https://lp-server1.lastplay.tv/app/MediaCover/${item.id}/poster-250.jpg`}
                            style={{ width: "42px", height: "75px" }}
                            alt={`${item.title} poster`}
                          />
                          {item.title}
                        </Link>
                      )}

                      {item.division === "movie" && item.hasFile === true && (
                        <Link
                          key={item.id}
                          className="stretched-link dropdowns"
                          to={{
                            pathname: `/movie/${item.id}`,
                            state: {
                              tmd: item.tmdbId,
                            },
                          }}
                        >
                          {" "}
                          <img
                            src={`https://lp-server1.lastplay.tv/mov/MediaCover/${item.id}/poster-250.jpg`}
                            style={{ width: "42px", height: "75px" }}
                            alt={`${item.title} poster`}
                          />
                          {item.title}
                        </Link>
                      )}
                    </ListGroup.Item>
                  )}
                  value={this.state.value}
                  onChange={this.Changing}
                  onSelect={(value) => this.setState({ value })}
                  onClick={(value) => this.setState({ value })}
                  menuStyle={menuStyle}
                  inputProps={{ placeholder: "Search..." }}
                />
              </ListGroup>
            </div>
          </Navbar.Brand>

          <Navbar.Toggle aria-controls="basic-navbar-nav" />

          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
              <Nav className="searchbar"></Nav>
            </Nav>

            <Nav className="nav_links">
              <ul className="nav_icons">
                <Nav.Link href="/dashboard">
                  <MdHome size="2em" />
                  <span>Home</span>
                </Nav.Link>

                <Nav.Link href="/tv">
                  <MdLiveTv size="2em" />
                  <span>Shows</span>
                </Nav.Link>

                <Nav.Link href="/movies">
                  <MdLocalMovies size="2em" />
                  <span>Movies</span>
                </Nav.Link>
                <Nav.Link href="/invite">
                  <MdShare size="2em" />
                  <span>Invite</span>
                </Nav.Link>
                <Nav.Link href="/contact">
                  <MdLiveHelp size="2em" />
                  <span>Help</span>
                </Nav.Link>
              </ul>
              <NavDropdown
                title={`Welcome, ${this.state.name}`}
                id="collasible-nav-dropdown"
                style={{ textTransform: "capitalize" }}
              >
                <NavDropdown.Item bg="dark" href="#" onClick={this.Logout}>
                  Logout
                </NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </div>
    );
  }
}

export default withRouter(Header);
