import React, { Component } from 'react'
import { Col, Row, Container} from 'react-bootstrap'
import Header from './header/header'

class Cast extends Component {
    constructor() {
        super()
        this.state = {
            credits: [],
            person: []
        }
    }

    componentDidMount() {
        fetch(`https://api.themoviedb.org/3/person/${this.props.match.params.id}?api_key=${process.env.REACT_APP_TMDB}&language=en-US`)
        .then(res => res.json())
            .then((data) =>{
                this.setState({ 
                    person: data
                })

    })
    fetch(`https://api.themoviedb.org/3/person/${this.props.match.params.id}/combined_credits?api_key=${process.env.REACT_APP_TMDB}&language=en-US`)
    .then(res => res.json())
        .then((data) =>{
            this.setState({ 
                credits: data.cast.sort(function(a,b) { return b.popularity - a.popularity})
            })

})

}
    render() {
        return (
            <>
            <Header />
            <Container><br />
            <Row className="justify-content-center">
                <img src={`https://image.tmdb.org/t/p/w185${this.state.person.profile_path}`} onError={(e)=>{e.target.onerror = null; e.target.src=`${process.env.PUBLIC_URL}/lpposter185.png`}} alt={`${this.state.person.name} Headshot`} />
            </Row>
            <Row className="justify-content-center">
            <h2>{this.state.person.name}</h2> 
            </Row>
            <Col>
        Born: {this.state.person.birthday}<br />{this.state.person.place_of_birth} <br /> <span><i>Occupation: {this.state.person.known_for_department}</i></span></Col>
        <p>
            {this.state.person.biography}
        </p>

        <Row  className="">

        {this.state.credits.filter(credit => credit.adult === false || !credit.adult).map(credit => {
        return (
            <Col>
                        <a href="!#" >
                    <img src={`https://image.tmdb.org/t/p/w185${credit.poster_path}`} alt="cast_poster" onError={(e)=>{e.target.onerror = null; e.target.src=`${process.env.PUBLIC_URL}/lpposter.png`}} style={{width:'100px', height:'150px'}}/><br />
                    <span>{credit.title || credit.name}</span><br />
            </a>
            </Col>
    
        )
})}
    </Row>
        
            </Container>

            </>
        
        )
    }
}


    export default Cast
  
