import React, { Component } from "react";
import Register from "./components/register/register";
import Login from "./components/login/login";
import Television from "./components/tv/television";
import Video from "./components/tv/video";
import Movie from "./components/movie/movie";
import Contact from "./components/contact/contact";
import Invite from "./components/invite/invite";
import MovDetail from "./components/movie/mov";
import Cast from "./components/cast";
import MovieAPanel from "./components/admin/index.js";
import Cookies from "js-cookie";
import Passwordreset from "./components/passwordreset";
import Passwordforgot from "./components/passwordforgot";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import ReactGA from "react-ga";
import { createBrowserHistory } from "history";
import Dashboard from "./components/dashboard/";
import Blog from "./components/blog/blog";

const history = createBrowserHistory();
history.listen((location) => {
  ReactGA.set({ page: location.pathname }); // Update the user's current page
  ReactGA.pageview(location.pathname); // Record a pageview for the given page
});

const trackingId = "UA-162249992-1"; // Replace with your Google Analytics tracking ID
ReactGA.initialize(trackingId);
ReactGA.set({
  userId: { localStorage },
  // any data that is relevant to the user session
  // that you would like to track with google analytics
});

const isLoggedIn = () => {
  if (localStorage.getItem("user") != null)
    return localStorage.getItem("TOKEN_KEY");
};

// Protected Route
const SecuredRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={(props) =>
      // ternary condition

      isLoggedIn() === undefined ? (
        <Route component={Login} />
      ) : (
        <Component {...props} />
      )
    }
  />
);
export default class App extends Component {
  render() {
    // const {pathname} = this.props.location;
    return (
      <Router>
        <Switch>
          <div>
            <Route exact path="/" component={Login} />
            <Route path="/register/:activated_token" component={Register} />
            <Route path="/login" component={Login} />
            <SecuredRoute
              path="/password/reset/:token"
              component={Passwordreset}
            />
            <SecuredRoute path="/password/forgot" component={Passwordforgot} />
            <SecuredRoute exact path="/tv" component={Television} />
            <SecuredRoute exact path="/movie/:id" component={MovDetail} />
            <SecuredRoute path="/movies" component={Movie} />
            <SecuredRoute path="/contact" component={Contact} />
            <SecuredRoute path="/invite" component={Invite} />
            <SecuredRoute exact path="/tv/:id" component={Video} />
            <SecuredRoute exact path="/person/:id" component={Cast} />
            <SecuredRoute path="/dashboard" component={Dashboard} />
            <SecuredRoute path="/admin" component={MovieAPanel} />
            <SecuredRoute path="/blog" component={Blog} />
          </div>
        </Switch>
      </Router>
    );
  }
}
