import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Col, Row, Alert, Container, Button } from "react-bootstrap";
import "./style.css";
class Featured extends Component {
  constructor() {
    super();
    this.state = {
      movies: [],
    };
  }

  componentDidMount() {
    fetch(`${process.env.REACT_APP_URL}/api/movie/featured`)
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          movies: data,
        });
        console.log(data);
      })
      .catch(console.log);
  }

  render() {
    {
      return (
        <div>
          <Row className="justify-content-center popular-header">
            <Container>
              <Alert variant="info">
                <b>Welcome back!</b>
                <br />
                We have been away and we've missed you. We hope to continue to
                provide you content through these times. #StayHome. Two things
                to note, we will now be testing streaming UFC events and you
                will notice the diminish of TV Shows available. We are taking a
                leap to support the shows that all of you love and want to
                binge. Therefore, we are dedicated to bring you the content that
                is necessary.
                <a href="/movies"> Please request your show here!</a>
                <br />
                <a href="/blog">Read More</a>
              </Alert>
            </Container>
          </Row>
          <Row className="popular-header justify-content-center">
            <h2>
              <svg
                width="1em"
                height="1em"
                viewBox="0 0 16 16"
                class="bi bi-bookmark-star-fill"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  d="M4 0a2 2 0 0 0-2 2v13.5a.5.5 0 0 0 .74.439L8 13.069l5.26 2.87A.5.5 0 0 0 14 15.5V2a2 2 0 0 0-2-2H4zm4.16 4.1a.178.178 0 0 0-.32 0l-.634 1.285a.178.178 0 0 1-.134.098l-1.42.206a.178.178 0 0 0-.098.303L6.58 6.993c.042.041.061.1.051.158L6.39 8.565a.178.178 0 0 0 .258.187l1.27-.668a.178.178 0 0 1 .165 0l1.27.668a.178.178 0 0 0 .257-.187L9.368 7.15a.178.178 0 0 1 .05-.158l1.028-1.001a.178.178 0 0 0-.098-.303l-1.42-.206a.178.178 0 0 1-.134-.098L8.16 4.1z"
                />
              </svg>{" "}
              Featured Event
            </h2>
          </Row>
          <Row className=" popular-header justify-content-center">
            {this.state.movies.slice(0, 4).map((movie) => {
              return (
                <Col xs={6} lg={3} className="popular" key={`${movie.id}`}>
                  <Link to={{ pathname: `/movie/${movie.id}` }}>
                    <img
                      src={`https://image.tmdb.org/t/p/w500${movie.poster}`}
                      style={{ width: "100%" }}
                      alt={`${movie.title} poster`}
                    />
                  </Link>
                </Col>
              );
            })}
            <a href="https://sports.lastplay.tv/">
              <img
                src="https://i2.wp.com/mmafightradio.com/wp-content/uploads/2021/02/UFC-258-Usman-vs-Burns-Feb-13-MMA-Fight-Coverage.jpg"
                alt="UFC POSTER"
              />
            </a>
          </Row>
        </div>
      );
    }
  }
}

export default Featured;
