import React from "react";
import "./style.css";
import JSPlayer from "./player";
import M from "materialize-css";
import { Tab, Col, Nav, Row, Container, Table } from "react-bootstrap";
import ListGroup from "react-bootstrap/ListGroup";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import Header from "../header/header";
import Report from "../videofunctions/report.js";
import WatchLater from "../videofunctions/watchlater.js";
import Favorite from "../videofunctions/favorite.js";
import { Link } from "react-router-dom";
import "./television.css";
import axios from "axios";

class Video extends React.Component {
  constructor(props) {
    super();
    let user = localStorage.getItem("user");
    this.state = {
      shows: [],
      episodes: [],
      tmdb: [],
      cast: [],
      loaded: false,
      not_visible: true,
      watchLater: [],
      favorite: [],
      recent: {
        season: 0,
        episode: 0,
      },
      name: user,
    };
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.state.watchLater[0] == !prevState.watchLater[0]) {
      fetch(`${process.env.REACT_APP_URL}/api/watchlater/get`, {
        headers: {
          "Content-Type": "application/json",
          Username: `${this.state.name}`,
        },
      })
        .then((res) => res.json())
        .then((data) => {
          this.setState({
            watchLater: data.filter(
              (x) => x.tmdbId === this.state.shows.tmdbId
            ),
          });
        });
    }
    if (this.state.favorite[0] == !prevState.favorite[0]) {
      let username = localStorage.getItem("user");
      fetch(`${process.env.REACT_APP_URL}/api/favorite/get`, {
        headers: {
          "Content-Type": "application/json",
          Username: `${this.state.name}`,
        },
      })
        .then((res) => res.json())
        .then((data) => {
          this.setState({
            favorite: data.filter((x) => x.tmdbId === this.state.shows.tmdbId),
          });
        });
    }
  }

  componentDidMount() {
    fetch(`${process.env.REACT_APP_URL}/api/show/${this.props.match.params.id}`)
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          shows: data[0],
        });
        return fetch(
          `https://api.themoviedb.org/3/tv/${this.state.shows.tmdbId}?api_key=${process.env.REACT_APP_TMDB}&append_to_response=credits`
        );
      })
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          tmdb: data,
          cast: data.credits,
          loaded: true,
        });
      });

    fetch(
      `https://lp-server1.lastplay.tv/app/api/episode?seriesId=${this.props.match.params.id}&apikey=${process.env.REACT_APP_SONARR}`
    )
      .then((res) => res.json())
      .then((data) => {
        data = data.filter((res3) => res3.seasonNumber > 0);
        this.setState({
          episodes: data,
        });
      });
    fetch(`${process.env.REACT_APP_URL}/api/watchlater/get`, {
      headers: {
        "Content-Type": "application/json",
        Username: `${this.state.name}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          watchLater: data.filter((x) => x.tmdbId === this.state.shows.tmdbId)
            .length,
        });
      });
    fetch(`${process.env.REACT_APP_URL}/api/favorite/get`, {
      headers: {
        "Content-Type": "application/json",
        Username: `${this.state.name}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          favorite: data.filter((x) => x.tmdbId === this.state.shows.tmdbId)
            .length,
        });
      });
    let username = localStorage.getItem("user");
    fetch(`${process.env.REACT_APP_URL}/api/recent/get`, {
      headers: {
        "Content-Type": "application/json",
        Username: `${username}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        let recent = data
          .reverse()
          .filter((title) => title.title === this.state.shows.title)[0];
        if (typeof recent !== "undefined") {
          this.setState({
            recent: data
              .reverse()
              .filter((title) => title.title === this.state.shows.title)[0],
            // loaded: true,
          });
        }
      });
    M.Tabs.init(this.Tabs);
  }

  appendData = (a) => {
    console.log(a);
    let link =
      "https://lp-server1.lastplay.tv" +
      a.episodeFile.path.replace(a.episodeFile.path.slice(0, 12), "");
    let pos = `http://image.tmdb.org/t/p/original${this.state.shows.background}`;
    this.setState({
      player: link,
      poster: pos,
      not_visible: false,
      currEpisode: a.episodeNumber + " - " + a.title,
      szn: a.seasonNumber,
      epi: a.episodeNumber,
    });
    let config = {
      headers: {
        "Content-Type": "application/json",
        username: `${this.state.name}`,
        id: `${this.state.shows.id}`,
        title: `${this.state.tmdb.name}`,
        poster: `${this.state.shows.poster}`,
        background: `${this.state.shows.background}`,
        tmdbId: `${this.state.shows.tmdbId}`,
        episode: `${a.episodeNumber}`,
        season: `${a.seasonNumber}`,
      },
    };
    axios
      .patch(`${process.env.REACT_APP_URL}/api/recent/add`, config)
      .then((res) => {
        if (res.data.result === "success") {
          this.setState({
            recent: {
              season: a.seasonNumber,
              episode: a.episodeNumber,
            },
          });
        }
      });
    this.useEffect();
  };

  useEffect = () => {
    window.scrollTo({
      top: 500,
      left: 0,
      behavior: "smooth",
    });
  };

  useEffectBottom = () => {
    window.scrollTo({
      top: 1500,
      left: 0,
      behavior: "smooth",
    });
  };

  render() {
    let i = 0;
    const responsive = {
      superLargeDesktop: {
        // the naming can be any, depends on you.
        breakpoint: { max: 4000, min: 3000 },
        items: 5,
      },
      desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 4,
      },
      tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 3,
      },
      mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 2,
        partialVisibilityGutter: 15,
      },
    };

    const style = this.state.not_visible
      ? { display: "none", padding: "10%" }
      : {};

    if (this.state.loaded === false) return null;
    else {
      return (
        <div style={{ backgroundColor: "#f1f1f1" }}>
          <Header />
          <div className="UpperHeader">
            <img
              src={`http://image.tmdb.org/t/p/original${this.state.shows.background}`}
              alt={`${this.state.shows.title} poster`}
              style={{ width: "100%" }}
            />
          </div>
          <Container>
            <Row>
              <Col className="postertab justify-content-center" md={3}>
                <img
                  src={`http://image.tmdb.org/t/p/w185${this.state.shows.poster}`}
                  alt={`${this.state.shows.title} poster`}
                />
                <Row style={{ paddingTop: "20px" }}>
                  <Col>
                    <Favorite
                      favorite={this.state.favorite}
                      movie={this.state.shows}
                      user={this.state.name}
                      id={this.props.match.params.id}
                    />
                  </Col>
                  <Col>
                    <WatchLater
                      watchlater={this.state.watchLater}
                      movie={this.state.shows}
                      user={this.state.name}
                      id={this.props.match.params.id}
                    />
                  </Col>
                </Row>
                <h3 style={{ paddingTop: "20px" }}>{this.state.shows.title}</h3>{" "}
                <br />
                <Table style={{ width: "50%", margin: "0px auto" }}>
                  <thead>
                    <tr>
                      <th>Network</th>
                      <th>Runtime</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>{this.state.tmdb.networks[0].name}</td>
                      <td>{this.state.tmdb.episode_run_time[0]}m</td>
                      <td>{this.state.tmdb.status}</td>
                    </tr>
                  </tbody>
                </Table>
                {this.state.tmdb.genres.map((genre) => {
                  return <span key={genre.id}> {genre.name} </span>;
                })}
                <hr />
                <div>
                  <span>
                    <svg
                      width="1em"
                      height="1em"
                      viewBox="0 0 16 16"
                      class="bi bi-bookmarks"
                      fill="currentColor"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        fill-rule="evenodd"
                        d="M2 4a2 2 0 0 1 2-2h6a2 2 0 0 1 2 2v11.5a.5.5 0 0 1-.777.416L7 13.101l-4.223 2.815A.5.5 0 0 1 2 15.5V4zm2-1a1 1 0 0 0-1 1v10.566l3.723-2.482a.5.5 0 0 1 .554 0L11 14.566V4a1 1 0 0 0-1-1H4z"
                      />
                      <path
                        fill-rule="evenodd"
                        d="M4.268 1H12a1 1 0 0 1 1 1v11.768l.223.148A.5.5 0 0 0 14 13.5V2a2 2 0 0 0-2-2H6a2 2 0 0 0-1.732 1z"
                      />
                    </svg>
                    <b>
                      Last Watched Episode:
                      <br />
                    </b>
                  </span>
                  {this.state.recent.season !== 0 ? (
                    <span>
                      {" "}
                      Season {this.state.recent.season}- Episode{" "}
                      {this.state.recent.episode}
                    </span>
                  ) : (
                    <span>None</span>
                  )}
                </div>
              </Col>
              <Col md={9} style={{ padding: "5%" }}>
                <div
                  className="display-data-Container"
                  style={style}
                  onContextMenu={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                  }}
                >
                  <h3>{this.state.currEpisode}</h3>

                  <JSPlayer
                    queue={this.state.showQueue}
                    szn={this.state.szn}
                    epi={this.state.epi}
                    source={this.state.player}
                    poster={this.state.poster}
                  ></JSPlayer>
                </div>
                <h4>Overview</h4>
                <span>{this.state.tmdb.overview}</span>
                <br />
                <hr />
                <h4>Cast</h4>
                <Row>
                  {this.state.cast.cast.slice(0, 5).map((cast) => {
                    let i = 0;
                    i++;
                    return (
                      <Col className="cast" key={cast.id}>
                        <Link
                          to={{
                            pathname: `/person/${cast.id}`,
                          }}
                        >
                          <img
                            src={`https://image.tmdb.org/t/p/w185${cast.profile_path}`}
                            onError={(e) => {
                              e.target.onerror = null;
                              e.target.src = `${process.env.PUBLIC_URL}/lpposter.png`;
                            }}
                            alt="Cast_Poster"
                            style={{ width: "100px", height: "150px" }}
                          />
                          <br />
                          <b>
                            <span>{cast.name}</span>
                          </b>
                          <br />
                          <i>{cast.character}</i>
                          <br />
                        </Link>
                      </Col>
                    );
                  })}
                </Row>
                <hr />
                <Row>
                  <Tab.Container
                    defaultActiveKey={`season${this.state.recent.season}`}
                  >
                    <Carousel
                      centerMode={false}
                      draggable={false}
                      partialVisible
                      responsive={responsive}
                      ssr={true} // means to render carousel on server-side.
                      infinite={true}
                      customTransition="all .5"
                      transitionDuration={500}
                      containerClass="carousel-container"
                      deviceType={this.props.deviceType}
                      itemClass="carousel-item-padding-40-px"
                    >
                      {this.state.episodes.map((episode) => {
                        if (this.state.tmdb.number_of_seasons > i) {
                          i++;
                          let seasonposter = null;
                          if (this.state.tmdb.seasons[0].name === "Specials") {
                            seasonposter = this.state.tmdb.seasons[i]
                              .poster_path;
                          } else {
                            seasonposter = this.state.tmdb.seasons[i - 1]
                              .poster_path;
                          }
                          return (
                            <div className="card" key={`${i}`}>
                              <Nav.Link eventKey={`season${i}`}>
                                <img
                                  variant="top"
                                  className="card-img"
                                  src={`https://image.tmdb.org/t/p/w342${seasonposter}`}
                                  alt="poster"
                                  onError={(e) => {
                                    e.target.src = `http://image.tmdb.org/t/p/w185${this.state.shows.poster}`; // some replacement image
                                  }}
                                />
                                <h2 style={{ maxHeight: "45px" }}>
                                  Season {i}{" "}
                                </h2>
                              </Nav.Link>
                            </div>
                          );
                        }
                        return null;
                      })}
                    </Carousel>
                    <ListGroup>
                      <Tab.Content className="seasonTabs">
                        {this.state.episodes.map((episode) => {
                          if (episode.hasFile === false) {
                            return (
                              <Tab.Pane
                                className="tabpanel"
                                eventKey={episode.seasonNumber}
                                key={episode.id}
                              >
                                <ListGroup.Item>
                                  <span
                                    style={{
                                      color: "red",
                                      fontStyle: "italic",
                                    }}
                                    href={null}
                                  >
                                    {episode.episodeNumber}. {episode.title}{" "}
                                    (Not yet available)
                                  </span>
                                </ListGroup.Item>
                              </Tab.Pane>
                            );
                          }

                          if (
                            episode.episodeNumber ==
                              this.state.recent.episode &&
                            episode.seasonNumber == this.state.recent.season
                          ) {
                            return (
                              <Tab.Pane
                                key={`tabs${i++}`}
                                eventKey={`season${episode.seasonNumber}`}
                                style={{ overflow: "hidden" }}
                                onClick={this.appendData.bind(this, episode)}
                              >
                                <span
                                  id={`season${episode.seasonNumber}`}
                                  style={{ cursor: "pointer" }}
                                  href={null}
                                >
                                  <ListGroup.Item
                                    style={{ backgroundColor: "#7ac8ff" }}
                                  >
                                    <p>
                                      {" "}
                                      <svg
                                        width="1em"
                                        height="1em"
                                        viewBox="0 0 16 16"
                                        class="bi bi-bookmark-fill"
                                        fill="currentColor"
                                        xmlns="http://www.w3.org/2000/svg"
                                      >
                                        <path
                                          fill-rule="evenodd"
                                          d="M2 2a2 2 0 0 1 2-2h8a2 2 0 0 1 2 2v13.5a.5.5 0 0 1-.74.439L8 13.069l-5.26 2.87A.5.5 0 0 1 2 15.5V2z"
                                        />
                                      </svg>
                                      {episode.episodeNumber}.{" "}
                                      <strong>{episode.title}</strong>{" "}
                                      <span className="float-right">
                                        <Report
                                          stream={episode.title}
                                          location={this.state.shows.title}
                                          user={this.state.name}
                                          platform={navigator.userAgent}
                                          vendor={navigator.vendor}
                                        />
                                      </span>
                                    </p>
                                    <span style={{ fontSize: "14px" }}>
                                      {episode.overview}
                                    </span>
                                  </ListGroup.Item>
                                </span>
                              </Tab.Pane>
                            );
                          } else {
                            return (
                              <Tab.Pane
                                className="tabpanel"
                                key={`tabs${i++}`}
                                eventKey={`season${episode.seasonNumber}`}
                                style={{ overflow: "hidden" }}
                                onClick={this.appendData.bind(this, episode)}
                              >
                                <span
                                  id={`season${episode.seasonNumber}`}
                                  style={{ cursor: "pointer" }}
                                  href={null}
                                >
                                  <ListGroup.Item>
                                    <p>
                                      {" "}
                                      {episode.episodeNumber}.{" "}
                                      <strong>{episode.title}</strong>{" "}
                                      <span className="float-right">
                                        <Report
                                          stream={episode.title}
                                          location={this.state.shows.title}
                                          user={this.state.name}
                                          platform={navigator.userAgent}
                                          vendor={navigator.vendor}
                                        />
                                      </span>
                                    </p>
                                    <span style={{ fontSize: "14px" }}>
                                      {episode.overview}
                                    </span>
                                  </ListGroup.Item>
                                </span>
                              </Tab.Pane>
                            );
                          }
                        })}
                      </Tab.Content>
                    </ListGroup>
                  </Tab.Container>
                </Row>
              </Col>
            </Row>
          </Container>
        </div>
      );
    }
  }
}

export default Video;
