import React, { Component } from 'react'
import { Col, Row, Container, Spinner} from 'react-bootstrap'
import { Link } from "react-router-dom";
import Header from '../header/header'
import InfiniteScroll from 'react-infinite-scroll-component'
import Newshows from './newshows'
class Television extends Component {
  constructor() {
      super()
      this.state = {
          shows: [],
          page: 1,
          scrolling: false, 
          totalPages: null

      }
  }

componentDidMount() {
this.loadShows()
this.scrollListener = window.addEventListener('scroll', (e) => {
})
}

loadShows = () => {
  fetch(`${process.env.REACT_APP_URL}/api/show?limit=40&page=${this.state.page}`)
  .then(res => res.json())
  .then((data)=> {
      console.log(data)
    this.setState({ 
        shows: [...this.state.shows, ...data.shows], //.sort(function(a, b) {
        //  return b.movieFile.dateAdded.localeCompare(a.movieFile.dateAdded);
       // })
       scrolling: false,
       totalPages: data.totalPages
    })
    console.log(this.state.shows)
})
}
loadMore = () => {
  this.setState(prevState => ({
    page: prevState.page + 1, 
    scrolling: true
  }), this.loadShows)
}

 render() {


    return (
  
<>
<Header />
<Newshows />
<InfiniteScroll
  dataLength={this.state.shows.length}
  next={this.loadMore}
  hasMore={true}
  loader={<h4>Loading...</h4>}
  > 
          <Row xs={3} style={{marginTop:"3%"}}className="showlibrary justify-content-center text-center">

{this.state.shows.map((show, index) => {
              return (
                <Col xs={6} id="show" className="show" sm key={`${show.id}`}>
                <Link to={{
                    pathname:`/tv/${show.id}`, state: {
                        tmd: show.tmdbId}}}>
                    <img src={`https://image.tmdb.org/t/p/w185/${show.poster}`} alt={`${show.title} poster`}/> <br />
                    <span>{show.title}</span>
                </Link>
            </Col>
              )}
          )}
            </Row>

</InfiniteScroll>
</>

    )
  } }

export default Television