import React, { Component } from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import swal from "sweetalert";
import { Col, Row, Container } from "react-bootstrap";
import "./passwordforgot.css";

const PasswordForgotSchema = Yup.object().shape({
  username: Yup.string().required("Username is required"),
});

class Passwordforgot extends Component {
  constructor(props) {
    super(props);

    this.state = {
      response: {},
      error_message: null,
    };
  }

  submitForm = (values, history) => {
    axios
      .post(`${process.env.REACT_APP_URL}/password/reset`, values)
      .then((res) => {
        console.log(res);
        if (res.data.result === "success") {
          swal("Success!", res.data.message, "success").then((value) => {
            //s window.location.reload();
          });
        } else if (res.data.result === "error") {
          swal("Error!", res.data.message, "error");
        }
      })
      .catch((error) => {
        console.log(error);
        swal("Error!", "Unexpected error", "error");
      });
  };
  showForm = ({
    values,
    errors,
    touched,
    handleChange,
    handleSubmit,
    isSubmitting,
  }) => {
    return (
      <Container fluid>
        <Row className="outside">
          <Col className="loginBox" xs={10} md={6} lg={3} xl={2}>
            <Row>
              <h1>
                <b>Last</b>Play<span style={{ fontSize: "14px" }}>.tv</span>
              </h1>
            </Row>
            <Row className="justify-content-center">
              <img src={process.env.PUBLIC_URL + "/logo100.png"} alt="logo" />
            </Row>
            <Row className="justify-content-center">
              <form style={{ marginTop: "5%" }} onSubmit={handleSubmit}>
                <label htmlFor="username" style={{ textAlign: "center" }}>
                  Confirm your username and we will send you instructions to
                  reset your password.
                </label>
                <Row className="justify-content-center">
                  <input
                    style={{
                      textAlign: "center",
                      marginBottom: "25px",
                      border: "0px",
                      borderBottom: "1px solid black",
                    }}
                    onChange={handleChange}
                    value={values.username}
                    type="username"
                    className={
                      errors.username && touched.username
                        ? "form-controls is-invalid"
                        : "form-controls"
                    }
                    id="username"
                    placeholder="Username"
                  />{" "}
                </Row>
                <Row className="justify-content-center">
                  {errors.username && touched.username ? (
                    <small id="passwordHelp" class="text-danger">
                      {errors.username}
                    </small>
                  ) : null}
                  {/* /.card-body */}
                </Row>
                <Col style={{ textAlign: "center" }}>
                  <button
                    type="submit"
                    disabled={isSubmitting}
                    class="btn btn-primary btn-group-lg"
                  >
                    Next
                  </button>
                </Col>
              </form>
            </Row>
          </Col>
        </Row>
      </Container>
    );
  };

  render() {
    return (
      <Formik
        initialValues={{
          username: "",
        }}
        onSubmit={(values, { setSubmitting }) => {
          this.submitForm(values, this.props.history);
          setSubmitting(false);
        }}
        validationSchema={PasswordForgotSchema}
      >
        {(props) => this.showForm(props)}
      </Formik>
    );
  }
}

export default Passwordforgot;
