import React, { Component } from 'react'
import { Col, Row, Container, Spinner} from 'react-bootstrap'
import { Link } from "react-router-dom";
import Header from '../header/header'
import InfiniteScroll from 'react-infinite-scroll-component'
import Request from './request'

class Movie extends Component {
  constructor() {
      super()
      this.state = {
          movies: [],
          page: 1,
          scrolling: false, 
          totalPages: null

      }
  }

componentDidMount() {
this.loadMovies()
this.scrollListener = window.addEventListener('scroll', (e) => {
})
}

handleScroll = (e) => { 
  const {scrolling, totalPages, page} = this.state
  if (scrolling) return
  if (totalPages <= page) return 
  const lastLi = document.querySelector("div.movielibrary > div:last-child")
  let lastLiOffset = []
  lastLiOffset = lastLi.offsetTop + lastLi.clientHeight
  const pageOffset = window.pageYOffset + window.innerHeight
  var bottomOffset = 20
  console.log(pageOffset)
  console.log('last'+lastLi)
  if (pageOffset > lastLiOffset - bottomOffset) this.loadMore()

}

loadMovies = () => {
  fetch(`${process.env.REACT_APP_URL}/api/movie?limit=40&page=${this.state.page}`)
  .then(res => res.json())
  .then((data)=> {
    this.setState({ 
        movies: [...this.state.movies, ...data.movies], //.sort(function(a, b) {
        //  return b.movieFile.dateAdded.localeCompare(a.movieFile.dateAdded);
       // })
       scrolling: false,
       totalPages: data.totalPages
    })
    console.log(this.state.movies)
})
}
loadMore = () => {
  this.setState(prevState => ({
    page: prevState.page + 1, 
    scrolling: true
  }), this.loadMovies)
}

 render() {


    return (
  
<>
<Header />
<Request />

<InfiniteScroll
  dataLength={this.state.movies.length}
  next={this.loadMore}
  hasMore={true}
  loader={<h4>Loading...</h4>}
  > 
          <Row xs={3} style={{marginTop:"3%"}}className="movielibrary justify-content-center text-center">

{this.state.movies.map((movie, index) => {
              return (
                <Col xs={6} id="movie" className="movie" sm key={`${movie.id}`}>
                <Link to={{
                    pathname:`/movie/${movie.id}`, state: {
                            tmd: movie.tmdbId}}}>
                    <img src={`https://image.tmdb.org/t/p/w185/${movie.poster}`} alt={`${movie.title} poster`}/> <br />
                    <span>{movie.title}</span>
                </Link>
            </Col>
              )}
          )}
            </Row>

</InfiniteScroll>
</>

    )
  } }

export default Movie