import React, { Component } from "react";
import axios from "axios";
import { Button } from "react-bootstrap";

class MovieAPanel extends Component {
  constructor() {
    super();
    this.state = {
      movies: [],
      movie: [],
      shows: [],
    };
  }

  componentDidMount() {
    fetch(
      `https://lp-server1.lastplay.tv/mov/api/movie?apikey=${process.env.REACT_APP_RADARR}`
    )
      .then((res) => res.json())
      .then((data) => {
        data = data.filter((m) => m.hasFile);
        data = data.sort(function (a, b) {
          return b.movieFile.dateAdded.localeCompare(a.movieFile.dateAdded);
        });

        this.setState({
          movies: data,
        });
        console.log(this.state.movies);
      });

    fetch(
      `https://lp-server1.lastplay.tv/app/api/series?apikey=${process.env.REACT_APP_SONARR}`
    )
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          shows: data,
        });
        console.log(this.state.shows);
      });
  }

  handleAddMovie = (value) => () => {
    console.log(value.tmdbId);
    fetch(
      `https://api.themoviedb.org/3/movie/${value.tmdbId}?api_key=${process.env.REACT_APP_TMDB}`
    )
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          tmdb: data,
        });
        console.log(this.state.tmdb);
        axios
          .post(`${process.env.REACT_APP_URL}/api/movie/add`, {
            id: value.id,
            vip: 0,
            featured: 0,
            title: `${value.title}`,
            poster: `${this.state.tmdb.poster_path}`,
            background: `${this.state.tmdb.backdrop_path}`,
            overview: `${this.state.tmdb.overview}`,
            year: `${value.year}`,
            trailer: `${value.youTubeTrailerId}`,
            release: `${this.state.tmdb.release_date}`,
            genres: this.state.tmdb.genres,
            imdb: `${value.imdbId}`,
            tmdb: `${value.tmdbId}`,
            on_server: `${value.hasFile.toString()}`,
            server: 1,
            drive: `${value.path.charAt(11)}`,
            file: `${value.folderName.slice(14)}/${
              value.movieFile.relativePath
            }`,
            created: `${value.added}`,
            updated: `${value.movieFile.dateAdded}`,
          })
          .then((res) => {
            console.log(res);
            if (res.status === 200 && res.data.message.name === "MongoError") {
              console.log(res.data.message.errmsg);
            } else {
              console.log("success");
            }
          })
          .catch((error) => {
            console.log(error);
          });
      });
  };

  handleDeleteMovie = (value) => () => {
    console.log(value.id);
    axios
      .delete(`${process.env.REACT_APP_URL}/api/movie/delete/${value.id}`)
      .then((res) => {
        console.log(res);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  handleAddShow = (value) => () => {
    console.log(value.title.slice(0, -4));
    fetch(
      `https://api.themoviedb.org/3/search/tv?api_key=${process.env.REACT_APP_TMDB}&language=en-US&page=1&query=${value.title}&include_adult=false`
    )
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          show: data.results[0],
        });
        console.log(this.state.show);
        axios
          .post(`${process.env.REACT_APP_URL}/api/show/add`, {
            id: value.id,
            featured: 0,
            title: `${this.state.show.original_name}`,
            poster: `${this.state.show.poster_path}`,
            background: `${this.state.show.backdrop_path}`,
            overview: `${this.state.show.overview}`,
            year: `${value.year}`,
            genres: this.state.show.genre_ids,
            imdb: `${value.imdbId}`,
            tmdb: `${this.state.show.id}`,
          })
          .then((res) => {
            console.log(res);
            if (res.status === 200) {
              console.log(res.data.message.errmsg);
            } else {
              console.log("success");
            }
          })
          .catch((error) => {
            console.log(error);
          });
      });
  };
  render() {
    const movies = this.state.movies;
    return (
      <>
        <style>{`
    table {
        font-family: arial, sans-serif;
        border-collapse: collapse;
        width: 100%;
      }
      
      td, th {
        border: 1px solid #dddddd;
        text-align: left;
        padding: 8px;
      }
      
      tr:nth-child(even) {
        background-color: #dddddd;
      }
  `}</style>
        <div style={{ width: "30%", float: "left" }}>
          <h3>MOVIE TABLE</h3>
          <table>
            <tr>
              <th>ACTION</th>
              <th>ID</th>
              <th>TITLE</th>
            </tr>

            {this.state.movies.map((movie) => (
              <tr>
                <td>
                  <Button
                    onClick={this.handleAddMovie(movie)}
                    variant="success"
                    type="submit"
                    size="sm"
                    block
                  >
                    Add
                  </Button>

                  <Button
                    onClick={this.handleDeleteMovie(movie)}
                    variant="danger"
                    type="submit"
                    size="sm"
                    block
                  >
                    Delete
                  </Button>
                </td>
                <td>{movie.id}</td>

                <td>{movie.title}</td>
              </tr>
            ))}
          </table>
        </div>
        <div style={{ width: "30%", float: "left" }}>
          <h3>TV TABLE</h3>
          <table>
            <tr>
              <th>ACTION</th>
              <th>ID</th>
              <th>TITLE</th>
            </tr>

            {this.state.shows.map((show) => (
              <tr>
                <td>
                  <Button
                    onClick={this.handleAddShow(show)}
                    variant="success"
                    type="submit"
                    size="sm"
                    block
                  >
                    Add
                  </Button>

                  <Button
                    onClick={this.handleAddShow(show)}
                    variant="danger"
                    type="submit"
                    size="sm"
                    block
                  >
                    Delete
                  </Button>
                </td>
                <td>{show.id}</td>

                <td>{show.title}</td>
              </tr>
            ))}
          </table>
        </div>
      </>
    );
  }
}

export default MovieAPanel;
